// src/components/Question.js
import React from 'react';
import PropTypes from 'prop-types';
import AnswerInput from './Answer';

export default function Question({ expression, correctAnswer, onAnswer }) {
  return (
    <div className="question">
      <p className="expression">What is: <strong>{expression}</strong> ?</p>
      <AnswerInput correctAnswer={correctAnswer} onAnswer={onAnswer} />
    </div>
  );
}

Question.propTypes = {
  expression: PropTypes.string.isRequired,
  correctAnswer: PropTypes.number.isRequired,
  onAnswer: PropTypes.func.isRequired,
};
