// src/components/AnswerInput.js
import React, { useState } from 'react';
import PropTypes from 'prop-types';

export default function AnswerInput({ correctAnswer, onAnswer }) {
  const [inputValue, setInputValue] = useState('');

  // Helper function to count digits ignoring negative sign
  const countDigits = (value) => {
    return value.toString().replace('-', '').length;
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Only submit when digit counts match
    if (countDigits(inputValue) === countDigits(correctAnswer)) {
      onAnswer(inputValue);
      setInputValue(''); // reset for next question
    }
  };

  return (
    <form onSubmit={handleSubmit}>
      <input
        type="number"
        value={inputValue}
        onChange={(e) => setInputValue(e.target.value)}
        placeholder="Your answer"
      />
      <button type="submit">Submit</button>
    </form>
  );
}

AnswerInput.propTypes = {
  correctAnswer: PropTypes.number.isRequired,
  onAnswer: PropTypes.func.isRequired,
};
