// src/components/MathGame.js
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import Question from './Question';
import History from './History';

export default function MathGame({ rounds, onGameOver, history, setHistory }) {
  const [currentRound, setCurrentRound] = useState(0);
  const [expression, setExpression] = useState('');
  const [correctAnswer, setCorrectAnswer] = useState(0);
  const [startTime, setStartTime] = useState(Date.now());

  useEffect(() => {
    generateNewExpression();
  }, []);

  const generateNewExpression = () => {
    const operators = ['+', '-', '*', '/'];
    const op = operators[Math.floor(Math.random() * operators.length)];
    let a = Math.floor(Math.random() * 20);
    let b = Math.floor(Math.random() * 20);

    if (op === '/' && b === 0) b = 1; // prevent divide by 0
    if (op === '/' && a % b !== 0) a = a - (a % b); // ensure integer result

    const expr = `${a} ${op} ${b}`;
    setExpression(expr);

    let answer;
    switch (op) {
      case '+':
        answer = a + b;
        break;
      case '-':
        answer = a - b;
        break;
      case '*':
        answer = a * b;
        break;
      case '/':
        answer = a / b;
        break;
      default:
        answer = 0;
    }
    setCorrectAnswer(answer);
    setStartTime(Date.now());
  };

  const handleAnswer = (answer) => {
    const timeTaken = Math.floor((Date.now() - startTime) / 1000);
    const correct = parseInt(answer, 10) === correctAnswer;

    const newEntry = {
      expression: expression,
      userAnswer: answer,
      correctAnswer: correctAnswer,
      time: timeTaken,
      correct: correct,
    };

    setHistory([...history, newEntry]);

    if (currentRound + 1 >= rounds) {
      onGameOver();
    } else {
      setCurrentRound(currentRound + 1);
      generateNewExpression();
    }
  };

  return (
    <div>
      <History history={history} />
      <Question
        expression={expression}
        correctAnswer={correctAnswer}
        onAnswer={handleAnswer}
      />
    </div>
  );
}

MathGame.propTypes = {
  rounds: PropTypes.number.isRequired,
  onGameOver: PropTypes.func.isRequired,
  history: PropTypes.array.isRequired,
  setHistory: PropTypes.func.isRequired,
};
